import React from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';

const TermosContainer = styled.div`
  min-height: 100vh;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  padding: 20px;
`;

const TermosCard = styled.div`
  max-width: 800px;
  margin: 0 auto;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
`;

const Header = styled.div`
  background: #28a745;
  color: white;
  padding: 30px;
  text-align: center;

  h1 {
    margin: 0;
    font-size: 2rem;
    font-weight: 600;
  }

  p {
    margin: 10px 0 0 0;
    opacity: 0.9;
    font-size: 1.1rem;
  }
`;

const Content = styled.div`
  padding: 40px;
  line-height: 1.6;
  color: #333;

  h2 {
    color: #28a745;
    margin: 30px 0 15px 0;
    font-size: 1.5rem;
    border-bottom: 2px solid #e9ecef;
    padding-bottom: 10px;
  }

  h3 {
    color: #495057;
    margin: 25px 0 10px 0;
    font-size: 1.2rem;
  }

  p {
    margin: 15px 0;
    text-align: justify;
  }

  ul {
    margin: 15px 0;
    padding-left: 30px;
  }

  li {
    margin: 8px 0;
  }

  .highlight {
    background: #fff3cd;
    padding: 15px;
    border-radius: 6px;
    border-left: 4px solid #ffc107;
    margin: 20px 0;
  }

  .contact-info {
    background: #f8f9fa;
    padding: 20px;
    border-radius: 8px;
    margin: 30px 0;
  }
`;

const BackButton = styled.button`
  background: #6c757d;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 6px;
  cursor: pointer;
  margin: 20px 40px 40px;
  font-size: 1rem;
  
  &:hover {
    background: #5a6268;
  }
`;

export const TermosUso = () => {
  const navigate = useNavigate();

  return (
    <TermosContainer>
      <TermosCard>
        <Header>
          <h1> Termos de Uso</h1>
          <p>Padaria Santa Marcelina - Cardpio Digital</p>
        </Header>

        <Content>
          <div className="highlight">
            <strong>ltima atualizao:</strong> 10 de outubro de 2025<br/>
            <strong>Data de vigncia:</strong> 10 de outubro de 2025
          </div>

          <h2>1. Aceitao dos Termos</h2>
          <p>
            Ao acessar e usar o cardpio digital da Padaria Santa Marcelina atravs do QR Code, 
            voc concorda em ficar vinculado a estes Termos de Uso. Se voc no concordar com 
            qualquer parte destes termos, no deve usar nosso servio.
          </p>

          <h2>2. Descrio do Servio</h2>
          <p>
            O cardpio digital  uma plataforma que permite aos clientes:
          </p>
          <ul>
            <li>Visualizar produtos disponveis na padaria</li>
            <li>Acessar informaes detalhadas sobre produtos</li>
            <li>Registrar preferncias pessoais (mediante consentimento)</li>
            <li>Exercer direitos relacionados  proteo de dados (LGPD)</li>
          </ul>

          <h2>3. Cadastro e Conta de Usurio</h2>
          <h3>3.1. Informaes Necessrias</h3>
          <p>
            Para usar alguns recursos do servio, voc pode precisar fornecer:
          </p>
          <ul>
            <li>Nome completo</li>
            <li>Email vlido</li>
            <li>Telefone para contato</li>
            <li>CPF (para identificao nica)</li>
            <li>Data de nascimento (opcional)</li>
          </ul>

          <h3>3.2. Veracidade das Informaes</h3>
          <p>
            Voc se compromete a fornecer informaes verdadeiras, precisas e atualizadas. 
             sua responsabilidade manter suas informaes atualizadas.
          </p>

          <h2>4. Uso Aceitvel</h2>
          <p>
            Voc concorda em usar o servio apenas para:
          </p>
          <ul>
            <li>Finalidades legais e legtimas</li>
            <li>Consultar informaes sobre produtos</li>
            <li>Exercer seus direitos de proteo de dados</li>
          </ul>

          <h3>4.1. Condutas Proibidas</h3>
          <p> expressamente proibido:</p>
          <ul>
            <li>Tentar acessar sistemas no autorizados</li>
            <li>Usar o servio para fins comerciais no autorizados</li>
            <li>Interferir no funcionamento normal do sistema</li>
            <li>Fornecer informaes falsas ou enganosas</li>
          </ul>

          <h2>5. Proteo de Dados (LGPD)</h2>
          <p>
            O tratamento dos seus dados pessoais est detalhado em nossa 
            <a href="/politica-privacidade" style={{color: '#28a745', textDecoration: 'underline'}}>
              Poltica de Privacidade
            </a>, que faz parte integrante destes termos.
          </p>

          <h2>6. Propriedade Intelectual</h2>
          <p>
            Todos os contedos do cardpio digital, incluindo textos, imagens, design e 
            funcionalidades, so propriedade da Padaria Santa Marcelina e esto protegidos 
            por leis de propriedade intelectual.
          </p>

          <h2>7. Limitao de Responsabilidade</h2>
          <p>
            A Padaria Santa Marcelina no se responsabiliza por:
          </p>
          <ul>
            <li>Interrupes temporrias do servio</li>
            <li>Problemas de conectividade de internet</li>
            <li>Uso indevido das informaes por terceiros no autorizados</li>
          </ul>

          <h2>8. Modificaes dos Termos</h2>
          <p>
            Reservamos o direito de modificar estes termos a qualquer momento. 
            As alteraes entraro em vigor imediatamente aps a publicao. 
            O uso continuado do servio aps modificaes constitui aceitao dos novos termos.
          </p>

          <h2>9. Cancelamento e Excluso</h2>
          <p>
            Voc pode solicitar a excluso dos seus dados a qualquer momento atravs do 
            portal LGPD. A Padaria Santa Marcelina tambm se reserva o direito de 
            suspender contas que violem estes termos.
          </p>

          <h2>10. Lei Aplicvel</h2>
          <p>
            Estes termos so regidos pelas leis brasileiras, incluindo:
          </p>
          <ul>
            <li>Lei Geral de Proteo de Dados (Lei 13.709/2018)</li>
            <li>Marco Civil da Internet (Lei 12.965/2014)</li>
            <li>Cdigo de Defesa do Consumidor (Lei 8.078/1990)</li>
          </ul>

          <div className="contact-info">
            <h2> Contato e Suporte</h2>
            <p><strong>Padaria Santa Marcelina</strong></p>
            <p> Email: contato@padariasantamarcelina.com.br</p>
            <p> Telefone: (11) 3456-7890</p>
            <p> Endereo: Rua das Delcias, 123 - So Paulo, SP</p>
            <p> Horrio de Atendimento: Segunda a Sexta, 8h s 18h</p>
            
            <div style={{marginTop: '20px', padding: '15px', background: '#e7f3ff', borderRadius: '6px'}}>
              <strong> Encarregado de Dados (DPO)</strong><br/>
              Email: lgpd@padariasantamarcelina.com.br<br/>
              Para exercer seus direitos LGPD e proteo de dados
            </div>
          </div>
        </Content>

        <BackButton onClick={() => navigate(-1)}>
           Voltar
        </BackButton>
      </TermosCard>
    </TermosContainer>
  );
};

export default TermosUso;